'use client'

import { useEffect, useState } from 'react'
import { io } from 'socket.io-client'
import { LineChart, Line, ResponsiveContainer } from 'recharts'
import { Globe, AlertCircle, CheckCircle2, Trash2 } from 'lucide-react'
import {
	AlertDialog,
	AlertDialogAction,
	AlertDialogCancel,
	AlertDialogContent,
	AlertDialogDescription,
	AlertDialogFooter,
	AlertDialogHeader,
	AlertDialogTitle,
	AlertDialogTrigger,
} from '@/components/ui/alert-dialog' // Путь может отличаться в зависимости от твоей структуры

interface MonitorCardProps {
	id: string
	name: string
	url: string
	status: 'UP' | 'DOWN' | 'PENDING'
	latency: number
	history: { ms: number }[]
}

export const MonitorCard = ({
	id,
	name,
	url,
	status: initialStatus,
	latency: initialLatency,
	history: initialHistory,
}: MonitorCardProps) => {
	const [currentStatus, setCurrentStatus] = useState(initialStatus)
	const [currentLatency, setCurrentLatency] = useState(initialLatency)
	const [currentHistory, setCurrentHistory] = useState(initialHistory)

	useEffect(() => {
		const socket = io(
			process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000'
		)
		socket.on(
			`status_updated:${id}`,
			(data: { status: 'UP' | 'DOWN'; latency: number }) => {
				setCurrentStatus(data.status)
				setCurrentLatency(data.latency)
				setCurrentHistory((prev) => {
					const newHistory = [...prev, { ms: data.latency }]
					if (newHistory.length > 15) newHistory.shift()
					return newHistory
				})
			}
		)
		return () => {
			socket.disconnect()
		}
	}, [id])

	const handleDelete = async () => {
		try {
			const response = await fetch(
				`${
					process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000'
				}/monitors/${id}`,
				{ method: 'DELETE' }
			)
			if (response.ok) window.location.reload()
		} catch (error) {
			console.error('Ошибка при удалении:', error)
		}
	}

	const isUp = currentStatus === 'UP'

	return (
		<div className='group relative bg-white border border-slate-200 rounded-xl p-5 shadow-sm hover:shadow-md transition-all overflow-hidden'>
			{/* Кнопка удаления с модальным окном */}
			<div className='absolute top-3 right-3 z-10'>
				<AlertDialog>
					<AlertDialogTrigger asChild>
						<button className='p-2 text-slate-400 hover:text-red-600 hover:bg-red-50 rounded-lg opacity-0 group-hover:opacity-100 transition-all'>
							<Trash2 size={18} />
						</button>
					</AlertDialogTrigger>
					<AlertDialogContent>
						<AlertDialogHeader>
							<AlertDialogTitle>Вы уверены?</AlertDialogTitle>
							<AlertDialogDescription>
								Это действие удалит мониторинг для <strong>{name}</strong> и всю
								историю проверок. Восстановить данные будет невозможно.
							</AlertDialogDescription>
						</AlertDialogHeader>
						<AlertDialogFooter>
							<AlertDialogCancel>Отмена</AlertDialogCancel>
							<AlertDialogAction
								onClick={handleDelete}
								className='bg-red-600 hover:bg-red-700'
							>
								Удалить
							</AlertDialogAction>
						</AlertDialogFooter>
					</AlertDialogContent>
				</AlertDialog>
			</div>

			<div className='flex items-start gap-4'>
				<div
					className={`shrink-0 p-3 rounded-lg ${
						isUp ? 'bg-green-50 text-green-600' : 'bg-red-50 text-red-600'
					}`}
				>
					<Globe size={24} />
				</div>

				{/* Контейнер с фиксом переполнения текста */}
				<div className='min-w-0 flex-1 pr-6'>
					<h3 className='font-bold text-slate-800 truncate' title={name}>
						{name}
					</h3>
					<p className='text-sm text-slate-500 truncate mt-0.5' title={url}>
						{url}
					</p>
				</div>
			</div>

			<div className='mt-6 flex items-end justify-between gap-4'>
				<div className='shrink-0'>
					<div className='flex items-center gap-2 mb-1'>
						<div
							className={`w-2 h-2 rounded-full animate-pulse ${
								isUp ? 'bg-green-500' : 'bg-red-500'
							}`}
						/>
						<p className='text-[10px] text-slate-400 uppercase font-bold tracking-tight'>
							Response Time
						</p>
					</div>
					<p className='text-2xl font-black text-slate-700'>
						{currentLatency}
						<span className='text-xs font-medium ml-1 text-slate-400'>ms</span>
					</p>
				</div>

				<div className='h-12 flex-1 max-w-[120px]'>
					<ResponsiveContainer width='100%' height='100%'>
						<LineChart data={currentHistory}>
							<Line
								type='monotone'
								dataKey='ms'
								stroke={isUp ? '#10b981' : '#ef4444'}
								strokeWidth={2}
								dot={false}
								isAnimationActive={false}
							/>
						</LineChart>
					</ResponsiveContainer>
				</div>
			</div>

			<div
				className={`mt-4 pt-4 border-t border-slate-50 flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest ${
					isUp ? 'text-green-600' : 'text-red-600'
				}`}
			>
				{isUp ? <CheckCircle2 size={14} /> : <AlertCircle size={14} />}
				{currentStatus}
			</div>
		</div>
	)
}
