'use client'
import { useEffect, useState } from 'react'
import { MonitorCard } from '@/src/entities/monitor/ui/monitor-card'
import axios from 'axios'

interface Monitor {
	id: string
	name: string
	url: string
	status: 'UP' | 'DOWN' | 'PENDING'
	// Мы добавим получение последних логов для истории
	checks: { responseTime: number }[]
}

export const MonitorList = () => {
	const [monitors, setMonitors] = useState<Monitor[]>([])
	const [loading, setLoading] = useState(true)
	const [error, setError] = useState<string | null>(null)

	useEffect(() => {
		const fetchMonitors = async () => {
			try {
				const { data } = await axios.get<Monitor[]>(
					`${process.env.NEXT_PUBLIC_API_URL}/monitors`
				)

				if (!Array.isArray(data)) {
					throw new Error('Invalid monitors response')
				}

				setMonitors(data)
			} catch (err: any) {
				console.error(err)
				setError(err.message || 'Ошибка при загрузке мониторов')
			} finally {
				setLoading(false)
			}
		}

		fetchMonitors()
	}, [])

	if (loading) return <div>Загрузка ресурсов...</div>
	if (error) return <div>Ошибка: {error}</div>

	return (
		<div className='grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6'>
			{monitors.map((monitor) => (
				<MonitorCard
					id={monitor.id}
					key={monitor.id}
					name={monitor.name}
					url={monitor.url}
					// Если PENDING, отображаем как UP или нейтрально, пока не прошла первая проверка
					status={monitor.status === 'PENDING' ? 'UP' : monitor.status}
					latency={monitor.checks?.[0]?.responseTime || 0}
					// Преобразуем формат БД в формат графика { ms: number }
					history={
						monitor.checks?.map((c) => ({ ms: c.responseTime })).reverse() || []
					}
				/>
			))}
		</div>
	)
}
