'use client'
import { ArrowUpRight, Activity, Zap, ShieldCheck } from 'lucide-react'

export const StatsOverview = () => {
	const stats = [
		{
			label: 'Avg. Uptime',
			value: '99.98%',
			icon: ShieldCheck,
			color: 'text-green-600',
		},
		{
			label: 'Avg. Latency',
			value: '142ms',
			icon: Zap,
			color: 'text-yellow-600',
		},
		{ label: 'Incidents', value: '2', icon: Activity, color: 'text-red-600' },
	]

	return (
		<div className='grid grid-cols-1 md:grid-cols-3 gap-6'>
			{stats.map((stat) => (
				<div
					key={stat.label}
					className='bg-white p-6 rounded-xl border border-slate-200 shadow-sm'
				>
					<div className='flex justify-between items-start'>
						<div>
							<p className='text-sm font-medium text-slate-500'>{stat.label}</p>
							<h3 className='text-3xl font-bold mt-1 text-slate-900'>
								{stat.value}
							</h3>
						</div>
						<div className={`p-2 rounded-lg bg-slate-50 ${stat.color}`}>
							<stat.icon size={20} />
						</div>
					</div>
					<div className='mt-4 flex items-center text-xs text-green-600 font-medium'>
						<ArrowUpRight size={14} className='mr-1' />
						<span>+2.1% с прошлого месяца</span>
					</div>
				</div>
			))}
		</div>
	)
}
